<template>
    <div>
        <el-card shadow="never" style="min-height: 600px;margin-bottom: 20px;">
            <el-collapse v-model="active" accordion>
                <el-collapse-item title="Talk is Cheap" name="code">
                    <el-carousel trigger="click" height="656px" :autoplay="false" @change="codeChange">
                        <el-carousel-item v-for="(item,index) in code.images" :key="'code'+index">
                            <div style="border-radius:5px; border: 1px solid #EBEEF5;height: 350px;">
                                <img :src="item" style="width: 100%;height:656px;">
                            </div>
                        </el-carousel-item>
                    </el-carousel>
                </el-collapse-item>
                <el-collapse-item title="Reading Digest" name="book">
                    <el-carousel trigger="click" height="775px" :autoplay="false" @change="bookChange">
                        <el-carousel-item v-for="(item,index) in book.images" :key="'book'+index">
                            <div style="border-radius:5px; border: 1px solid #EBEEF5;height: 775px;">
                                <img :src="item" style="width: 100%;height:775px;">
                            </div>
                        </el-carousel-item>
                    </el-carousel>
                </el-collapse-item>
                <el-collapse-item title="Easy Time" name="develop">
                    <div style="padding: 10px;line-height: 30px;color: #303133;font-size: 16px">
                        There will be some songs in the future.
                    </div>
                </el-collapse-item>
            </el-collapse>
        </el-card>
    </div>
</template>
<script>

    export default {
        data() {
            return {
                active: "code",
                code: {
                    images: [
                        require('../../assets/code0.jpg'),
                        require('../../assets/code1.jpg'),
                        require('../../assets/code2.jpg')
                    ],
                    status: "process",
                    active: 0,
                },
                book: {
                    images: [
                        require('../../assets/book0.jpg'),
                        require('../../assets/book1.jpg'),
                        require('../../assets/book2.jpg')
                    ],
                    status: "process",
                    active: 0,
                }
            }
        },
        mounted() {

        },
        methods: {
            codeChange(index) {
                this.code.status = index == 2 ? "success" : "process"
                this.code.active = index
            },
            bookChange(index) {
                this.book.status = index == 2 ? "success" : "process"
                this.book.active = index
            }
        }
    }
</script>


